import React, { Component } from 'react';
import './Project.css';
import {Container, Row, Col} from 'react-bootstrap';
import Jaytees from '../Jaytees.jpg';
import Blog from '../Blog.jpg';


export default class Project extends Component {
    render(){
        return (
          <Container className="work">
                <div className="title-page">
                  <h1>Projects Done</h1><hr className="horizontal"/>
              </div>
              <Row  className="victor">
                  <Col xs={12} sm={12} md={6} className="rend" >
                  <img className="done" src={Blog} />
                  <h5> Ellaordiah</h5>
                  <p className="test">Joint Blog Design</p>
                  </Col>
                  <Col xs={12} sm={12}  md={6} className="rend">
                      <img className="done" src={Jaytees}/>
                      <h5> Jaytees</h5>
                      <p className="test">Website Restructing</p>
                  </Col>

              </Row>


          </Container>
    )
}
}
