import React, { Component } from 'react';
import { Row, Col, Container, ProgressBar } from 'react-bootstrap';
import './Service.css';
import './Home.css';
import Web from '../Web.png';
import facilitator from '../facilitator.png'
import social from '../social.png'
import uiux from '../uiux.png'



export default class Service extends Component {
    render(){
        return (
            <Container className="Services" >

              <div className="title-page">
                  <h1>Services</h1><hr className="horizontal"/>
              </div>
            {/* <div className="row"> */}

              <Row className="row">
                    <Col xs={12} sm={12} md={12} className="render">
                   
                      <img src={Web} className="profile-pic"/>
                      <h5>Responsive Websites</h5>
                       <ul>
                        <li>Build websites that looks good across all devices</li>
                        <li>Write and edit web content</li>
                        <li>Create user-friendly design and clear navigation.</li>
                        <li>Maintain websites built</li>
                        <li>Study of Analytics</li>
                        <li>Fixes bug and ensure websites are searchable</li>
                      </ul>
                    </Col>
            
                    <Col xs={12} sm={12} md={12} className="render">
                      <img src={facilitator} className="profile-pic profile-pic1"/>
                      <h5 className="title">Coding Facilitator</h5>
                      <ul>
                        <li>Teach kids tech skills.</li>
                        <li>Perceive which requirements are amenable to for a certain age range - (Developing Program calendar).</li>
                         <li>Administering and reviewing progress on assessments.</li>
                        <li>Organizing the program</li>
                          <li>Follow up on each student</li>
                            <li>Kids’ mentors</li>
                      </ul>
                    </Col>
            
                    <Col xs={12}sm={12} md={12} className="render">
                      <img src={uiux} className="profile-pic profile-pic1"/>
                      <h5>UI-UX Designer</h5>
                      <ul>
                        <li> Develop UI mockups and prototypes that clearly illustrate how sites functions</li>
                        <li>Plan and implement new designs </li>
                        <li>Communicate with clients to understand their business goals</li>
                        <li>Combine creativity with an awareness of the design elements.  </li>
                        <li>Adhering to style standards on typography and graphic design.</li>
                      </ul>
                    </Col>
             </Row>
            {/* </div> */}

            <div className="skills">
              <h1>Skills</h1><hr className="horizontal"/>
              
                <Row className="new">
                <Col xs={12} sm={4} className="bar">
                 <ProgressBar className="progress" striped variant="success" animated now={95} label={`HTML`}/>
                 <ProgressBar className="progress" striped variant="warning" animated  now={85} label={`BootStrap`} />
                 <ProgressBar className="progress" striped variant="info" animated  now={70} label={`WordPress`} /> 
                 <ProgressBar className="progress" striped variant="danger" animated  now={50} label={`React JS`} />
                               
                 </Col>
                 <Col xs={12} sm={4} className="bar"> 
                 <ProgressBar className="progress" striped variant="success" animated  now={90} label={`CSS`} />
                 <ProgressBar className="progress" striped variant="warning" animated  now={80} label={`JavaScript`} />
                 <ProgressBar className="progress" striped variant="info" animated  now={60} label={`UI UX `} />
                 <ProgressBar className="progress" striped variant="danger" animated  now={30} label={`PHP`} />  
                 
                 
                 
             
                 </Col>

          </Row>
</div>

             </Container>
   
    )
}
}
