import React, { Component } from 'react';
import { Navbar, Nav, NavItem, Container } from 'react-bootstrap';
import { Link } from 'react-router-dom';

import './CustomNavBar.css';


export default class CustomNavBar extends Component {
    render(){
        return (
            <Container className="menu">
            <Navbar className="Kemi" default collapseOnSelect >
                    <Nav>
                    {/* //  style={{ display: "flex" }} > */}
                        <NavItem className="Emy">
                        <Link to="/">
                            Home
                            </Link>
                        </NavItem>
                        
                        <NavItem className="Emy">
                        <Link to="/service">
                            Services
                            </Link>
                        </NavItem>
                        <NavItem className="Emy" >
                        <Link to="/project">
                            Projects
                            </Link>
                        </NavItem>
                        
                        <NavItem className="Emy" >
                        <Link to="/contact">
                            Contact
                            </Link>
                        </NavItem>

                    </Nav>
               
            </Navbar>
            </Container>
        )
    }
}